#contact-content{
	text-align: left;
	width: 50vw;
	margin-left: 25vw;
	margin-top: 10vh;
}

#contact-table {
	border-collapse: collapse;
	width: 100%;
	text-align: center;
}

td{
	padding: 8px;
	height: 2vw;
	font-size: 1.5vw;
}

.TableButton{
	border: none;
	outline: none;
	background-color: rgba(0, 0, 0, 0);
	font-size: 1.5vw;
	color: rgba(168, 208, 230, 1);
}

.No-Design{
	width: 22vw;
	border: none;
	background-color: rgba(0, 0, 0, 0);
	font-size: 1.5vw;
	color: rgba(255, 255, 255, 1);
}

.No-Design:focus{
	outline: none;
}

.TableButton:hover{
	color: rgba(248, 233, 161, 1);
}


@media only screen and (max-width: 600px) {
	#contact-content{
		width: 100vw;
		margin-left: 0px;
	}

	td{
		height: 5vw;
		font-size: 3.5vw;
	}

	.TableButton{
		font-size: 3.5vw;
	}

	.No-Design{
		font-size: 3.5vw;
		width: 50vw;
	}
}


@media only screen and (max-width: 1208px) {
	#contact-content{
		width: 80vw;
		margin-left: 10vw;
	}

	td{
		height: 4vw;
		font-size: 2.5vw;
	}

	.TableButton{
		font-size: 2.5vw;
	}

	.No-Design{
		font-size: 2.5vw;
		width: 50vw;
	}
}