#home-content{
	text-align: left;
	margin-left: 10vw;
	margin-top: 10vh;
}

strong #name{
	font-size: 8vh;
	padding: 0;
}

strong #position{
	font-size: 4vh;
}

.ContactButtons{
	width: 8vh;
	height: 8vh;
	font-size: 4vh;
	background-color: rgba(0, 0, 0, 0);
	outline: none;
	border: none;
	color: rgba(168, 208, 230, 1);
}

.ContactLink{
	width: 8vh;
	height: 8vh;
	font-size: 4vh;
	background-color: rgba(0, 0, 0, 0);
	color: rgba(168, 208, 230, 1);
	margin-right: 2vw;
}

.ContactButtons:hover, .ContactLink:hover{
	color: rgba(248, 233, 161, 1);
	font-size: 2.5vw;
}