html, body {
    margin: 0;
    height: 100%;
    width: 100%;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
	background: -webkit-linear-gradient(150deg, #24305E 60%, #F76C6C 60%);
	background: -o-linear-gradient(150deg, #24305E 60%, #F76C6C 60%);
	background: -moz-linear-gradient(150deg, #24305E 60%, #F76C6C 60%);
	background: linear-gradient(150deg, #24305E 60%, #F76C6C 60%);
	background-repeat: no-repeat;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

#contents{
	width: 100vw;
	height: 84vh;
	margin-top: 16vh;
	text-align: center;
	color: rgba(255, 255, 255, 1);
	float: left;
}