#navBar{
	width: 100vw;
	height: 16vh;
	float: right;
	text-align: center;
	position: fixed;	
}

.myLogo{
	float: left;
	width: 20vw;
	text-align: center;
}

img{
	height: 6vw;
	width: 6vw;
}

mark{
	font-size: 2vw;
	color: rgba(255, 255, 255, 1);
	background-color: rgba(0, 0, 0, 0);
}

.navBarButtonsActive{
	float: left;
	width: 15vw;
	height: 7vh;
	color: rgba(247, 108, 108, 1);
	font-size: 6vh;
	border: none;
	text-decoration: none;
	padding-top: 1vh;
	outline: none;
}

.navBarButtonsInactive{
	float: left;
	width: 15vw;
	height: 7vh;
	color: rgba(168, 208, 230, 1);
	font-size: 4vh;
	border: none;
	text-decoration: none;
	padding-top: 1vh;
	outline: none;
}

.navBarButtonsActive:hover, .navBarButtonsInactive:hover{
	color: rgba(248, 233, 161, 1);
	font-size: 6vh;
}
@media only screen and (max-width: 500px) {
	.navBarButtonsActive{
		font-size: 1vh;
		width: 20vw;
	}

	.navBarButtonsInactive{
		font-size: 0.5vh;
	}

	.navBarButtonsActive:hover, .navBarButtonsInactive:hover{
		font-size: 1vh;
		width: 20vw;
	}
}

@media only screen and (max-width: 600px) {
	.navBarButtonsActive{
		font-size: 2.5vh;
		width: 20vw;
	}

	.navBarButtonsInactive{
		font-size: 2vh;
	}

	.navBarButtonsActive:hover, .navBarButtonsInactive:hover{
		font-size: 2.5vh;
		width: 20vw;
	}
}

@media only screen and (max-width: 1208px) {
	.navBarButtonsActive{
		font-size: 3.5vh;
		width: 20vw;
	}

	.navBarButtonsInactive{
		font-size: 3vh;
	}

	.navBarButtonsActive:hover, .navBarButtonsInactive:hover{
		font-size: 3.5vh;
		width: 20vw;
	}
}